import React, { useContext } from "react";

import passwordGenarator from '../../img/project-image/password-generator.png'
import devDitective from '../../img/project-image/dev-detective.png'
import discordClone from '../../img/project-image/discord-clone.png'
import razorpayClone from '../../img/project-image/razore-pay-clone.png'
import { themeContext } from "../../Context";
import "./Portfolio.css";

import { Swiper, SwiperSlide   } from "swiper/react";
// import {  Navigation ,Pagination } from "swiper";
import "swiper/css";
import 'swiper/css/autoplay';



import 'swiper/swiper-bundle.css';


// SwiperCore.use([Autoplay]);


const Portfolio = () => {
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;


  return (
    <div className="portfolio" id="portfolio">
      {/* heading */}
      <span style={{ color: darkMode ? "white" : "" }}>Recent Projects</span>
      <span>Portfolio</span>

      {/* slider */}
      <Swiper
     
        spaceBetween={30}
        slidesPerView={1}
        grabCursor={true}
        autoplay={{
          delay: 2000, // Set the delay between slides (in milliseconds)
          disableOnInteraction: false, // Set to false to continue autoplay after user interaction
        }}
        className="portfolio-slider"
      
      >
        <SwiperSlide  >
          <img src={devDitective} alt="" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={passwordGenarator} alt="" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={razorpayClone} alt="" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={discordClone} alt="" />
        </SwiperSlide>
      </Swiper>
    </div>
  );
};

export default Portfolio;
